import React from "react";
import c from "./Item.module.css";
import DescriptionMap from "./DescriptionMap";

const Description = (props) => {
    return (<div className={c.item}>
        <div className={c.imgBlock}>
            <img className={c.img} src={props.currentItem.img} alt='img'/>
        </div>
        <div className={c.shortDescription}>
            <div className={c.title}>{props.currentItem.model}</div>
            <div className={c.text}><span>Ціна:</span> {props.currentItem.price}$</div>
            {props.currentItem.memory &&
            <div className={c.text}><span>Розмір пам'яті:</span> {props.currentItem.memory}gb</div>}
            {props.currentItem.color && <div className={c.text}><span>Колір:</span> {props.currentItem.color}$</div>}
            {props.currentItem.display &&
            <div className={c.text}><span>Розмір екрану:</span> {props.currentItem.display}$</div>}
            {props.currentItem.size &&
            <div className={c.text}><span>Розмір ремінця:</span> {props.currentItem.size}мм.</div>}
            {props.currentItem.opmemory &&
            <div className={c.text}><span>Розмір оперативної пам'яті:</span> {props.currentItem.opmemory}$</div>}
            <DescriptionMap currentItem={props.currentItem} addItemOrderThunk={props.addItemOrderThunk}
                            addNewFastOrder={props.addNewFastOrder}/>
        </div>
    </div>);
}

export default Description;