import React from "react";
import c from './Search.module.css';
import Product from "../../Content/Product/Product";
import {NavLink} from "react-router-dom";

const Search = (props) => {
    return (<div className={c.page}>
        <NavLink to={'/'} className={c.back}>
            {'<На головну'}
        </NavLink>
        <h1>Результати пошуку</h1>
        {props.searchResults.length < 1 ?
            <h2>Нажаль за заданими даними товари відсутні, спробуйте будь-ласка щераз</h2> :
                <Product data={props.searchResults} addNewFastOrder={props.addNewFastOrder}/>}

            </div>);
        }

        export default Search;