import React from "react";
import c from "./Header.module.css";
import applelogo from "../../../images/default/applelogo.jpg";
import {NavLink} from "react-router-dom";
import SearchIcon from "./SearchIcon";

const TopHeader = (props) => {
    return (<div className={c.top}>
        <div>
            <div><img className={c.applogo} src={applelogo} alt={'logo'}/></div>
        </div>

        <div className={c.links}>
            <div><NavLink to='/contacts' className={c.contacts}>Контакти</NavLink></div>
            <div><NavLink to='/guarantee' className={c.contacts}>Гарантія</NavLink></div>
            <div><NavLink to='/delivery' className={c.contacts}>Доставка</NavLink></div>
        </div>

        <div className={c.topitem}>
            <span className={c.number}> &#9990; 0(88) 88 - 888 -88</span>
        </div>
        <SearchIcon search={props.search}/>
    </div>);
}

export default TopHeader;