import React from "react";
import c from './Bottom.module.css';
import {NavLink} from "react-router-dom";

const Bottom = (props) => {
    return (<div className={c.bottom}>

        <div className={c.links}>
            <div><NavLink to='/contacts' className={c.contacts}>Контакти</NavLink></div>
            <div><NavLink to='/guarantee' className={c.contacts}>Гарантія</NavLink></div>
            <div><NavLink to='/delivery' className={c.contacts}>Доставка</NavLink></div>
        </div>

        <div className={c.info}>
            <h3> Графік роботи: </h3>
            <p> Понеділок - Субота: 10:00- 20:00 год </p>
            <p> Неділя: 12:00 - 18:00 год </p>
        </div>

        <div className={c.info}>2015 - 2020 Магазин техніки</div>
    </div>);
}

export default Bottom;