import React from "react";
import c from "./Cart.module.css";
import close from '../../images/default/close.png'
import InputForm from "./InputForm";

const CartItem = (props) => {
    const setCurrentValue = (value, model) => {
        props.changeAmountThunk(value, model);
    };
    const deleteItem = (id) => {
        props.deleteItemFromOrderThunk(id);
    };

    return (<div>
        {props.items.map(e => <div className={c.order} key={e.id}>

            <div className={c.imgblock}><img src={e.img} alt='img'/></div>

            <div className={c.info}>
                <h4>{e.model}</h4>
                <div className={c.text}><span>Ціна:</span> {e.price}$</div>
                <div className={`${c.text} ${c.quantity}`}><span>Кількість:</span>
                    <InputForm amount={e.amount} setCurrentValue={setCurrentValue} model={e.model}
                               quantity={e.quantity}/>
                </div>
                <div className={c.text}><span>Загальна ціна: </span>{e.totalPrice}$</div>
            </div>

            <div><span className={c.close} onClick={() => deleteItem(e.model)}><img src={close} alt='a'/></span></div>

        </div>)}
        <h4 className={c.marginBottom}>Загальна вартість:  {props.totalPurchasePrice}$ </h4>

    </div>);
};

export default CartItem;