import c from './SearchModal.module.css';
import {Field, reduxForm} from "redux-form";
import {Input} from "../../FormComponents/FormComponents";
import {emptyField} from "../../FormComponents/Validators";
import React from "react";


const Form = (props) => {
    return <form onSubmit={props.handleSubmit}>
        <div className={c.formItem}><Field component={Input} name={'data'}
                                                              validate={[emptyField]}/>
        </div>
        <div>
            <button className={c.close}>Пошук</button>
        </div>
    </form>
}

export  const SearchForm = reduxForm({
    form: 'cart'
})(Form);