import React from 'react';
import './App.css';
import Header from "./Components/Header/GeneralHeader/HeaderContainer";
import {Route, Switch, withRouter} from "react-router-dom";
import ProductContainer from './Components/Content/Product/ProductPage/ProductsCatalogContainer';
import ItemContainer from "./Components/Content/Item/ItemContainer";
import {compose} from "redux";
import Cart from "./Components/Cart/CartContainer";
import OrdersPage from "./Components/Admin/ordersPageContainer";
import Bottom from "./Components/Bottom/Bottom";
import MainPage from "./Components/Content/MainPage/MainPageContainer";
import NotFoundPage from "./Components/Other/NotFound/NotFoundPage";
import Contacts from "./Components/Other/Contacts/Contacts";
import Guarantee from "./Components/Other/Guarantee/Guarantee";
import Delivery from "./Components/Other/Delivery/Delivery";
import Search from "./Components/Header/Search/SearchContainer";

function App() {
    return (<div className='app'>
            <Header/>
            <Bottom/>
            <div>
                <Switch>
                    <Route path='/search' component={Search}/>
                    <Route path='/contacts' component={Contacts}/>
                    <Route path='/guarantee' component={Guarantee}/>
                    <Route path='/delivery' component={Delivery}/>
                    <Route path='/admin' component={OrdersPage}/>
                    <Route path='/cart' component={Cart}/>
                    <Route path='/catalog/:section?' render={() => <ProductContainer/>}/>
                    <Route path='/product/:section?/:model?' render={() => <ItemContainer/>}/>
                    <Route exact path='/' render={() => <MainPage/>}/>
                    <Route exact path='/mystore' render={() => <MainPage/>}/>
                    <Route path='*' render={() => <NotFoundPage/>}/>
                </Switch>
            </div>
        </div>
    );
}

export default compose(
    withRouter
)(App);
