import React from "react";
import {slide as Menu} from "react-burger-menu";
import c from './BurgerMenu.module.css';
import iphone from "../../../images/default/iphone.png";
import ipad from "../../../images/default/ipad.png";
import imac from "../../../images/default/mac.png";
import iwatch from "../../../images/default/iwatch.png";
import other from "../../../images/default/other.png";
import admin from "../../../images/default/admin.jpg";
import {NavLink} from "react-router-dom";

const BurgerMenu = (props) => {
    return (<div>
            <Menu isOpen={props.isOpen} className={c.burger} width={280} onStateChange={props.isMenuOpen}>
                <div className={c.title}>КАТАЛОГ ТОВАРІВ</div>
                <div className={c.box}>
                    <div className={c.imgcont}><img className={c.logoImg} src={iphone} alt='logo'/></div>
                    <NavLink to='/catalog/iphone' onClick={props.toggleMenu} className={c.navItem}
                             activeClassName={c.active}>Iphone</NavLink>
                </div>
                <div className={c.box}>
                    <div className={c.imgcont}><img className={c.logoImg} src={ipad} alt='logo'/></div>
                    <NavLink to='/catalog/ipad' onClick={props.toggleMenu}
                             className={c.navItem} activeClassName={c.active}>Ipad</NavLink>
                </div>
                <div className={c.box}>
                    <div className={c.imgcont}><img className={c.logoImg} src={imac} alt='logo'/></div>
                    <NavLink to='/catalog/mac' onClick={props.toggleMenu} className={c.navItem}
                             activeClassName={c.active}>Mac</NavLink>
                </div>
                <div className={c.box}>
                    <div className={c.imgcont}><img className={c.logoImg} src={iwatch} alt='logo'/></div>
                    <NavLink to='/catalog/watch' onClick={props.toggleMenu} className={c.navItem}
                             activeClassName={c.active}>Watch</NavLink>
                </div>
                <div className={c.box}>
                    <div className={c.imgcont}><img className={c.logoImg} src={other} alt='logo'/></div>
                    <NavLink to='/catalog/accestories' onClick={props.toggleMenu} className={c.navItem}
                             activeClassName={c.active}> Аксесуари </NavLink>
                </div>
                <div className={c.adminbox}>
                    <div className={c.imgcont}><img className={c.logoImg} src={admin} alt='logo'/></div>
                    <NavLink to='/admin' onClick={props.toggleMenu} className={c.navItem}
                             activeClassName={c.active}>Admin</NavLink>
                </div>
            </Menu>
        </div>
    );
}

export default BurgerMenu;