import React, {useState} from "react";
import Product from "../Product";
import c from './ProductsCatalog.module.css'
import FilterForm from "../Filter/FilterForm";
import Sort from "./Sort";

const ProductsCatalog = (props) => {
    const title = props.section;
    let newTitle = '';
    if (props.section === 'accestories') {
        newTitle = 'Аксесуари';
    } else {
        newTitle = title[0].toUpperCase() + title.slice(1);
    }

    const [filter, setFilter] = useState(false);
    const setButFilt = () => {
        filter ? setFilter(false) : setFilter(true)
    }

    return (<div className={c.page}>
        <div className={c.pcPage}>
            <div className={c.pcFilter}>
                <FilterForm filter={props.filter} section={title}
                            inputsData={props.inputsData} addInputData={props.addInputData}/>
            </div>
            <div>
                <div className={c.title}>
                    <div className={c.mobButton}>
                        <button onClick={() => setButFilt()}>Фільтрувати</button>
                    </div>
                    <Sort sort={props.sort}/>
                </div>
                <div className={c.mobileFilter}>
                    {filter && <FilterForm filter={props.filter} section={title}
                                           inputsData={props.inputsData} addInputData={props.addInputData}/>}
                </div>
                <h1><span>{newTitle}</span></h1>
                {props.data.length === 0 ? <h2>Нажаль за заданими параметрами товарів не знайдено</h2> :
                    <Product data={props.data} addNewFastOrder={props.addNewFastOrder}/>
                }
            </div>
        </div>
    </div>);
}

export default ProductsCatalog;