import {addNewFastOrder} from "../../../../Redux/order-reducer";
import {connect} from "react-redux";
import React from "react";
import {addInputData, filter, setCurrentSection, sort} from "../../../../Redux/data-reducer";
import {compose} from "redux";
import {withRouter} from "react-router-dom";
import ProductsCatalog from "./ProductsCatalog";

class ProductsCatalogContainer extends React.Component {
    uploadItem() {
        let section = this.props.match.params.section;
        this.props.setCurrentSection(section);
    }

    componentDidMount() {
        this.uploadItem();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.section !== prevProps.match.params.section) {
            this.uploadItem();
        }

    }

    render(){
        return <ProductsCatalog data={this.props.data} addNewFastOrder={this.props.addNewFastOrder}
                                sort={this.props.sort} filter={this.props.filter} section={this.props.match.params.section}
                                 addInputData={this.props.addInputData} inputsData={this.props.inputsData}
        />
    }
}

const mapStateToProps = (state) => {
    return {
        data: state.data.currentSection,
        inputsData: state.data.inputsData
    }
}

export default compose(
    withRouter,
    connect(mapStateToProps, {addNewFastOrder, setCurrentSection, sort, filter, addInputData}))
(ProductsCatalogContainer);