import React, {useState} from "react";
import c from "./ProductsCatalog.module.css";
import Select from "react-select";

const options = [
    {value: 'model', label: 'За назвою A-Z', order: 1, section:'model'},
    {value: 'model2', label: 'За назвою Z-A', order: -1, section:'model'},
    {value: 'price', label: 'Від дешевих до дорогих',order: 1, section:'price'},
    {value: 'price2', label: 'Від дорогих до дешевих',order: -1, section:'price'},
    {value: 'quantity', label: 'За наявністю',order: -1, section:'quantity'}
];

const Sort = (props) => {
    const [selected, setSelected] = useState(null);
    const onChange = (selected) => {
        setSelected(selected);
        props.sort(selected.section, selected.order);
    }
    return (<div className={c.select}>
        <Select
            value={selected}
            onChange={onChange}
            options={options}
            placeholder={'Сортувати...'}
        />
    </div>);
}

export default Sort;
