import React, {useState} from "react";
import c from "./Header.module.css";
import burger from "../../../images/default/burger.png";
import close from "../../../images/default/close.png";
import BurgerMenu from "../Burgermenu/BurgerMenu";
import {NavLink} from "react-router-dom";
import cart from "../../../images/default/cart.jpg";
import logo from "../../../images/default/logo.PNG";

const HeaderBottom = () => {
    const [isOpen, openMenu] = useState(false);
    const isMenuOpen = function () {
        return isOpen;
    };
    const toggleMenu = () => {
        isOpen ? openMenu(false) : openMenu(true);
    }
    return (<div className={c.bottom}>
        <div>
            <div className={c.burger}>
                <span onClick={toggleMenu}>
                    {!isOpen && <img className={c.burgerimg} src={burger} alt='burger'/>}
                    {isOpen && <img className={c.burgerimg} src={close} alt='close'/>}
                    <BurgerMenu top={110} onClick={toggleMenu} isOpen={isOpen} isMenuOpen={isMenuOpen}/></span>
            </div>
        </div>
        <div><NavLink to={'/'}>
            <img className={c.logo} src={logo} alt='burger'/>
        </NavLink></div>
        <div className={c.cart}>
            <NavLink className={c.nav} to={'/cart'}>
                <img className={c.cartlogo} src={cart} alt={'cart'}/>
            </NavLink>
        </div>
    </div>);
}

export default HeaderBottom;