import React from "react";
import c from './Item.module.css';
import {NavLink} from "react-router-dom";
import Description from "./Description";

const Item = (props) => {


    return (<div className={c.page}>
        <div className={c.backContainer}><NavLink to={`/catalog/${props.currentItem.type}`} className={c.back}>
            {'<Назад'}
        </NavLink></div>
        <Description currentItem={props.currentItem} addItemOrderThunk={props.addItemOrderThunk} addNewFastOrder={props.addNewFastOrder}/>
        <div><h1>Опис</h1>
            <div className={c.desc}>{props.currentItem.description}</div>
        </div>
    </div>);
};

export default Item;