import React, {useState} from "react";
import c from "./Item.module.css";
import {NavLink} from "react-router-dom";
import OneClickModalWindow from "../../ModalWindows/OneClickModalWindow/OneClickModalWindow";
import ThankModal from "../../ModalWindows/ThankModalWindow/ThankModal";

const DescriptionMap = (props) => {
    const buy = () => {
        props.addItemOrderThunk(props.currentItem);
    };
    const [formModalIsOpen, setFormIsOpen] = useState(false);
    const [thankModalIsOpen, setThankIsOpen] = useState(false);
    const toggleFormModal = () => {
        formModalIsOpen ? setFormIsOpen(false) : setFormIsOpen(true);
    }
    const toggleThankModal = () => {
        thankModalIsOpen ? setThankIsOpen(false) : setThankIsOpen(true);
    }
    const onSubmit = (formData) => {
        props.addNewFastOrder('addNewInform', formData, props.currentItem.model);
        toggleFormModal();
        toggleThankModal();
    };
    return (<div>{
        props.currentItem.quantity > 0 ? <div>
                {props.currentItem.quantity === 1 ? <div className={c.textRed}>Залишився тільки 1</div> :
                    <div className={c.aval}><span>Внаявноті</span></div>
                }
                <NavLink to={'/cart'}>
                    <div className={c.buttoncont}>
                        <button onClick={buy} className={c.but}>Купити</button>
                    </div>
                </NavLink></div>
            :
            <div>
                <div className={c.aval}><span>Відсутній у продажі</span></div>
                <div className={c.buttoncont}>
                    <button onClick={() => toggleFormModal()} className={c.but}>Повідомити про наявність</button>
                </div>
            </div>}
        <OneClickModalWindow closeModal={toggleFormModal} modalIsOpen={formModalIsOpen} onSubmit={onSubmit}/>
        <ThankModal modalIsOpen={thankModalIsOpen} closeModal={toggleThankModal}/>
    </div>);

}

export default DescriptionMap;