import c from "./Product.module.css";
import {NavLink} from "react-router-dom";
import React, {useState} from "react";
import OneClickModalWindow from "../../ModalWindows/OneClickModalWindow/OneClickModalWindow";
import ThankModal from "../../ModalWindows/ThankModalWindow/ThankModal";

const Product = (props) => {
    const [formModalIsOpen, setFormIsOpen] = useState(false);
    const [order, setOrder] = useState({});
    const [thankModalIsOpen, setthankIsOpen] = useState(false);
    const buyInOneClick = (model, type) => {
        setOrder({model: model, type: type});
        toggleFormModal();
    };
    const toggleFormModal = () => {
        formModalIsOpen ? setFormIsOpen(false) : setFormIsOpen(true);
    }
    const toggleThankModal = () => {
        thankModalIsOpen ? setthankIsOpen(false) : setthankIsOpen(true);
    }
    const onSubmit = (formData) => {
        props.addNewFastOrder(order.type, formData, order.model);
        toggleFormModal();
        toggleThankModal();
    };
    return (<div className={c.content}>
        {props.data.map(p => <div className={c.item} key={p.id}>
            <NavLink to={`/product/${p.type}/${p.model}`} className={c.text}>
                <div className={c.imgContainer}><img className={c.img} src={p.img} alt='img'/></div>
                <div className={c.model}>  {p.model} </div>
            </NavLink>
            <div>{p.price}$</div>
            <div className={c.but}>
                {p.quantity > 0 ?
                    <button onClick={() => buyInOneClick(p.model, 'addFastOrder')}>Купити в 1 клік</button> :
                    <button onClick={() => buyInOneClick(p.model, 'addNewInform')}>Повідомити про наявність</button>
                }
            </div>
        </div>)}
        <OneClickModalWindow closeModal={toggleFormModal} modalIsOpen={formModalIsOpen} onSubmit={onSubmit}/>
        <ThankModal modalIsOpen={thankModalIsOpen} closeModal={toggleThankModal}/>
    </div>);
};

export default Product;