import React from "react";
import c from './FilterForm.module.css';

const FilterForm = (props) => {

    const onChange = (event) => {
        props.addInputData(event.target.name, event.target.value);
        props.filter();
    }

    return (<form onSubmit={props.handleSubmit} className={c.form}>
        <div className={c.formItem}>
            <div className={c.title}>Ціна:</div>
            <div>
                <input className={c.input} placeholder={'від'} value={props.inputsData.lprice} onChange={onChange}
                       name="lprice"/>
            </div>
            <div className={c.formItem}>
                <input className={c.input} placeholder={'до'} value={props.inputsData.hprice} onChange={onChange}
                       name="hprice"/>
            </div>
        </div>
        {props.section === 'iphone' || props.section === 'ipad' || props.section === 'mac' ?
            <div className={c.formItem}>
                <div className={c.title}>Об'єм пам'яті:</div>
                <div><input className={c.radio} name="memory" onChange={onChange} type="radio"
                            value={'32'} checked={props.inputsData.memory === '32'}/> 32gb
                </div>
                <div><input className={c.radio} name="memory" onChange={onChange} type="radio"
                            value={'64'} checked={props.inputsData.memory === '64'}/> 64gb
                </div>
                <div><input className={c.radio} name="memory" onChange={onChange} type="radio"
                            value={'128'} checked={props.inputsData.memory === '128'}/> 128gb
                </div>
                <div><input className={c.radio} name="memory" onChange={onChange} type="radio"
                            value={'256'} checked={props.inputsData.memory === '256'}/> 256gb
                </div>
                <div><input className={c.radio} name="memory" onChange={onChange} type="radio"
                            value={'512'} checked={props.inputsData.memory === '512'}/> 512gb
                </div>
            </div> : null}

        {props.section === 'watch' && <div className={c.formItem}>
            <div className={c.title}>Розмір ремінця:</div>
            <div><input className={c.radio} name="size" onChange={onChange} type="radio"
                        value={'38'} checked={props.inputsData.size === '38'}/> 38 мм.
            </div>
            <div><input className={c.radio} name="size" onChange={onChange} type="radio"
                        value={'40'} checked={props.inputsData.size === '40'}/> 40 мм.
            </div>
            <div><input className={c.radio} name="size" onChange={onChange} type="radio"
                        value={'42'} checked={props.inputsData.size === '42'}/> 42 мм.
            </div>
            <div><input className={c.radio} name="size" onChange={onChange} type="radio"
                        value={'44'} checked={props.inputsData.size === '44'}/> 44 мм.
            </div>
        </div>}
    </form>);

}

export default FilterForm;

